'use client';

import { useEffect, useState } from 'react';
import  supabase  from '@/lib/supabase';
import SupplierModal from './SupplierModal';
import Link from 'next/link';

interface Supplier {
  id?: string;
  name: string;
  status: string;
  contact_name: string;
  email: string;
  phone: string;
  address: string;
  city: string;
  state: string;
  postal_code: string;
  country: string;
  notes: string;
}
const emptySupplier: Supplier = {
  name: '',
  status: 'active',
  contact_name: '',
  email: '',
  phone: '',
  address: '',
  city: '',
  state: '',
  postal_code: '',
  country: '',
  notes: '',
};


export default function SuppliersPage() {
  const [suppliers, setSuppliers] = useState<Supplier[]>([]);
  const [selectedSupplier, setSelectedSupplier] = useState<Supplier | null>(null);
  const [search, setSearch] = useState('');
  const [statusFilter, setStatusFilter] = useState('All');

  const fetchSuppliers = async () => {
    const { data, error } = await supabase.from('suppliers').select('*');
    if (!error) setSuppliers(data || []);
  };
  

  useEffect(() => {
    fetchSuppliers();
  }, []);

  const filtered = suppliers.filter((s) => {
    const matchText = `${s.name} ${s.contact_name} ${s.email}`.toLowerCase();
    return (
      matchText.includes(search.toLowerCase()) &&
      (statusFilter === 'All' || s.status.toLowerCase() === statusFilter.toLowerCase())
    );
  });

  return (
    <div className="p-6">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-semibold">Suppliers</h1>
        <button
          className="bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
          onClick={() => setSelectedSupplier({} as Supplier)}
        >
          + Add Supplier
        </button>
      </div>

      {/* Search + Filter */}
      <div className="flex gap-4 mb-6">
        <input
          type="text"
          placeholder="Search suppliers..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="border px-3 py-2 rounded w-full max-w-md"
        />
        <select
          value={statusFilter}
          onChange={(e) => setStatusFilter(e.target.value)}
          className="border px-3 py-2 rounded"
        >
          <option value="All">All Statuses</option>
          <option value="Active">Active</option>
          <option value="Inactive">Inactive</option>
        </select>
      </div>

      {/* Supplier Cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {filtered.map((s) => (
          <div key={s.id} className="bg-white border rounded-lg p-6 shadow-sm relative">
            <div className="absolute top-4 right-4">
              <span className="text-xs bg-blue-500 text-white px-2 py-1 rounded">
                {s.status}
              </span>
            </div>

            <h2 className="font-bold text-lg">{s.name}</h2>
            <p className="text-sm text-gray-600 mb-2"> {s.contact_name}</p>

            <p className="text-sm text-gray-700">üìû {s.phone}</p>
            <p className="text-sm text-gray-700">‚úâÔ∏è {s.email}</p>
            <p className="text-sm text-gray-700">üìç {s.city}, {s.country}</p>

            <div className="flex justify-between mt-4">
              <button
                onClick={() => setSelectedSupplier(emptySupplier)}
                className="text-sm px-4 py-1 border rounded hover:bg-gray-100"
              >
                View Details
              </button>
              <Link
                href={`/admin/suppliers/${s.id}/products`}
                className="text-sm text-blue-600 hover:underline"
              >
                View Products
              </Link>
            </div>
          </div>
        ))}
      </div>

      {/* Modal */}
      {selectedSupplier && (
        <SupplierModal
          supplier={selectedSupplier}
          onClose={() => {
            setSelectedSupplier(null);
            fetchSuppliers(); // g√ºncelleme sonrasƒ± liste yenile
          }}
        />
      )}
    </div>
  );
}
